.wine-loader {
  margin: auto;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.wine-loader .wine__icon {
  width: 64px;
  height: auto;
  position: relative;
}
.wine-loader .wine__icon::before {
  content: "";
  width: 80px;
  height: 80px;
  padding: 10px;
  position: absolute;
  border: 1px solid #eeeee8;
  background: #cecec5;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  z-index: 2;
  box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.15);
}
.wine-loader .wine__icon::after {
  content: "";
  width: 80px;
  height: 80px;
  background: #eeeee8;
  display: block;
  border: 1px solid rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  z-index: 2;
}
.wine-loader .wine__icon svg {
  width: 100%;
  height: auto;
  position: relative;
  z-index: 3;
}
